"use client";

import { useState} from "react";
import ButtonLogOut from "../components/ButtonLogOut";
import CheckBoxPrincipal from "../components/CheckBoxPrincipal";

export default function Home() {

  const [novaTarefa, setNovaTarefa] = useState("");

  async function handleSubmit(e: React.FormEvent) {
    e.preventDefault();
    const token = localStorage.getItem("stark_token");

    const response = await fetch("http://127.0.0.1:8000/api/projetos/", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${token}`,
      },
      body: JSON.stringify({ anotacao: novaTarefa }),
    });

    if (response.ok) {
      setNovaTarefa("");
      carregarTarefas();
    }
  }



  return (
    <div className="w-full flex min-h-screen items-center p-10 justify-center">
      <div className="w-full flex flex-col items-center justify-center gap-6">
        <form onSubmit={handleSubmit} className="w-full flex justify-center gap-2 ">
          <input
            value={novaTarefa}
            onChange={(e) => setNovaTarefa(e.target.value)}
            type="text"
            placeholder="Digite a tarefa"
            className="bg-stone-300 w-[75%] p-3 rounded-[.6rem] outline-[#0C7075] focus:outline-2"
          />
          <button
            type="submit"
            className="bg-[#0C7075] duration-150 hover:bg-[#26494a] text-white px-4 py-2 rounded-[.8rem] cursor-pointer"
          >
            Adicionar
          </button>
        </form>
        <div className="w-full h-auto bg-[#0C7075] p-5 rounded-3xl ">
          <CheckBoxPrincipal/>
        </div>
        <ButtonLogOut />
      </div>
    </div>
  );
}
